import React, { Fragment } from 'react';
import classes from './Users.module.css';
const user = (props) => {
    return (
        <Fragment>
            <div>
                <div className={classes.Users}>
                    <h2 className='mb-4'><b>{props.name}</b></h2>
                    <p>Name:&nbsp;{props.name}</p>
                    <p>Age:&nbsp;{props.age}</p>
                    <p>City:&nbsp;{props.city}</p>
                </div>
                <p>
                    {props.children}
                </p>
            </div>
        </Fragment>
    )
};
export default user;