import React, { Fragment } from 'react';
import Container from 'react-bootstrap/Container';
import classes from './UsersNameLabel.module.css'
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

const UsersNameLabel = props => {
    return (
        <Fragment>
            <Container>
                <Row>
                    <Col md={4} className={classes.NameLable}>
                        <label>{props.name}</label>
                    </Col>
                    <Col md={{ span: 4, offset: 4 }}>
                        <button className={classes.Details} onClick={props.click}>Click here to <br /> see more</button>
                    </Col>
                </Row>
                <Row>
                    {props.children}
                </Row>
            </Container>
        </Fragment>
    );
}

export default UsersNameLabel;