import React, { Component, Fragment } from 'react';
import classes from './App.module.css';
import Users from '../components/Users/Users';
import UsersNameLabel from '../components/UsersNameLabel/UsersNameLabel';
import Card from 'react-bootstrap/Card';
import Accordion from 'react-bootstrap/Accordion'

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      users: [
        { id: 'bbacd', name: 'Mike', city: 'LA', age: 20 },
        { id: '45vgb', name: 'Tyson', city: 'NY', age: 50 }
      ],
      showUsers: false,
      userId: ''
    };
  };

  toggleUsersHandler = (id) => {
    const doesShow = this.state.showUsers;
    this.setState({ showUsers: !doesShow, userId: id });
  };

  render() {
    let users = null;
    if (this.state.showUsers) {
      users = (
        <div>
          {this.state.users.filter((user) => user.id === this.state.userId).map((user, index) => {
            return <Users
              name={user.name}
              age={user.age}
              city={user.city}
              key={user.id}
            />
          })}
        </div>
      );
    };

    return (
      <Fragment>
        <Accordion defaultActiveKey="0">
          <Card className="text-center" border="dark" eventKey="0">
            <Card.Header className={classes.CardHeader}>User Details</Card.Header>
            <Card.Body className={classes.CardBody}>
              {
                this.state.users.map((user) =>
                  <UsersNameLabel
                    key={user.id}
                    name={user.name}
                    click={() => this.toggleUsersHandler(user.id)}
                  />
                )
              }
            </Card.Body>
            <Accordion.Collapse eventKey="0" className={classes.AccordionDetails}>
              <Card.Body>{users}</Card.Body>
            </Accordion.Collapse>
          </Card>
        </Accordion>
      </Fragment>
    );
  };
};
export default App;
